import store from '../store'
import { Message, MessageBox } from 'element-ui'

export function init(params, callbacks, modules) {
  var appKey = params.appKey;
  var token = params.token;
  var navi = params.navi || "";

  modules = modules || {};
  var RongIMLib = modules.RongIMLib || window.RongIMLib;
  var RongIMClient = RongIMLib.RongIMClient;
  var protobuf = modules.protobuf || null;

  var config = {};

  //navi '120.92.10.214:8888'
  if(navi !== "") {
    config.navi = navi;
  }

  //api, '172.20.210.38:81:8888'
  var api = params.api || "";
  if(api !== "") {
    config.api = api;
  }

  //support protobuf url + function
  if(protobuf != null) {
    config.protobuf = protobuf;
  }


  RongIMLib.RongIMClient.init(appKey,null,config);

  var instance = RongIMClient.getInstance();

  // 
  RongIMClient.setConnectionStatusListener({
    onChanged: function (status) {
      console.log(status);
      switch (status) {
        case RongIMLib.ConnectionStatus["CONNECTED"]:
        case 0:
          console.log("")
          callbacks.getInstance && callbacks.getInstance(instance);
          break;

        case RongIMLib.ConnectionStatus["CONNECTING"]:
        case 1:
          console.log("")
          break;

        case RongIMLib.ConnectionStatus["DISCONNECTED"]:
        case 2:
          console.log("")
          break;

        case RongIMLib.ConnectionStatus["NETWORK_UNAVAILABLE"]:
        case 3:
          console.log("")
          Message({
            message: '',
            type: 'error',
            duration: 5 * 1000
          })
          break;

        case RongIMLib.ConnectionStatus["CONNECTION_CLOSED"]:
        case 4:
          console.log("")
          Message({
            message: '',
            type: 'error',
            duration: 5 * 1000
          })
          break;

        case RongIMLib.ConnectionStatus["KICKED_OFFLINE_BY_OTHER_CLIENT"]:
        case 6:
          console.log("")
          Message({
            message: '',
            type: 'error',
            duration: 5 * 1000
          })
          break;

        case RongIMLib.ConnectionStatus["DOMAIN_INCORRECT"]:
        case 12:
          console.log("")
          break;
      }
    }
  });

  /*
   http://www.rongcloud.cn/docs/web.html#3
   
   1
   2 conversationType + targetId
   3
   4http://rongcloud.cn/docs/api/js/index.html
   */
  RongIMClient.setOnReceiveMessageListener({
    // 
    onReceived: function (message) {
      // 
      switch(message.messageType){
        case RongIMClient.MessageType.TextMessage:
        case RongIMClient.MessageType.VoiceMessage:
        case RongIMClient.MessageType.ImageMessage:
        case RongIMClient.MessageType.RichContentMessage:
          break;
        case RongIMClient.MessageType.DiscussionNotificationMessage:
          // message.content.extension => 
          break;
        case RongIMClient.MessageType.LocationMessage:
          // message.content.latiude => 
          // message.content.longitude => 
          // message.content.content =>  base64
          break;

        case RongIMClient.MessageType.InformationNotificationMessage:
          // do something...
          break;
        case RongIMClient.MessageType.ContactNotificationMessage:
          // do something...
          break;
        case RongIMClient.MessageType.ProfileNotificationMessage:
          // do something...
          break;
        case RongIMClient.MessageType.CommandNotificationMessage:
          // do something...
          break;
        case RongIMClient.MessageType.CommandMessage:
          // do something...
          break;
        case RongIMClient.MessageType.UnknownMessage:
          // do something...
          break;
        default:
        // do something...
      }
      callbacks.receiveNewMessage && callbacks.receiveNewMessage(message);
    }
  });

  //
  RongIMClient.connect(token, {
    onSuccess: function(userId) {
      callbacks.getCurrentUser && callbacks.getCurrentUser({userId:userId});
      console.log("id" + userId);
      store.commit('SET_INIT_STATUS', true)
    },
    onTokenIncorrect: function() {
      console.log('token');
    },
    onError:function(errorCode) {
      console.log(errorCode);
    }
  });
}



