import { Message } from 'element-ui'

let msgTypes = {
  1: RongIMLib.ConversationType.PRIVATE,            // 
  2: RongIMLib.ConversationType.DISCUSSION,         // 
  3: RongIMLib.ConversationType.GROUP,              // 
  4: RongIMLib.ConversationType.CHATROOM,           // 
  5: RongIMLib.ConversationType.CUSTOMER_SERVICE,   // 
  6: RongIMLib.ConversationType.SYSTEM,             // 
  7: RongIMLib.ConversationType.APP_PUBLIC_SERVICE, // 
  8: RongIMLib.ConversationType.PUBLIC_SERVICE      //  () 
}

export function getConversations() {
  return new Promise((resolve, reject) => {
    RongIMClient.getInstance().getConversationList({
      onSuccess: function (list) {
        resolve(list)
      },
      onError: function (error) {
        reject(error)
      }
    }, null)
  })
}

export function getTotalUnreadCount() {
  return new Promise((resolve, reject) => {
    RongIMClient.getInstance().getTotalUnreadCount({
      onSuccess: function (count) {
        resolve(count)
      },
      onError: function (error) {
        reject(error)
      }
    })
  })
}

export function clearUnreadCount(msgType, targetId) {
  let conversationType = msgTypes[msgType]
  return new Promise((resolve, reject) => {
    RongIMClient.getInstance().clearUnreadCount(conversationType, targetId,{
      onSuccess:function(){
        resolve()
      },
      onError:function(error){
        reject(error)
      }
    });
  })
}

/**
 *
 * @param msgType
 * @param targetId
 * @param timestrap //  null 0 ,timestrap = 0;
 * @param count //  0-20 
 */
export function getHistoryMsg(msgType, targetId, timestrap = null, count = 20) {
  let conversationType = msgTypes[msgType]
  return new Promise((resolve, reject) => {
    RongIMLib.RongIMClient.getInstance().getHistoryMessages(conversationType, targetId, timestrap, count, {
      onSuccess: function (list, hasMsg) {
        resolve([list, hasMsg])
      },
      onError: function (error) {
        console.log("GetHistoryMessages,errorcode:" + error);
        reject(error)
      }
    })
  })
}

export function sendImageMsg(msgType, targetId, message, at) {
  let rcMsg = new RongIMLib.ImageMessage(message)
  return sendMsg(msgType, targetId, rcMsg, at).then(message => {
    return message
  })
}

export function sendTextMsg(msgType, targetId, message, at) {
  let rcMsg = new RongIMLib.TextMessage(message)
  return sendMsg(msgType, targetId, rcMsg, at).then(message => {
    return message
  })
}

let sendMsg = function (msgType, targetId, rcMsg, at) {
  let conversationtype = msgTypes[msgType]
  return new Promise((resolve, reject) => {
    RongIMClient.getInstance().sendMessage(conversationtype, targetId, rcMsg, {
        onSuccess: function (message) {
          //message Id
          console.log("Send successfully");
          resolve(message)
        },
        onError: function (errorCode, message) {
          let info = '';
          switch (errorCode) {
            case RongIMLib.ErrorCode.TIMEOUT:
              info = '';
              break;
            case RongIMLib.ErrorCode.UNKNOWN_ERROR:
              info = '';
              break;
            case RongIMLib.ErrorCode.REJECTED_BY_BLACKLIST:
              info = '';
              break;
            case RongIMLib.ErrorCode.NOT_IN_DISCUSSION:
              info = '';
              break;
            case RongIMLib.ErrorCode.NOT_IN_GROUP:
              info = '';
              break;
            case RongIMLib.ErrorCode.NOT_IN_CHATROOM:
              info = '';
              break;
            default :
              info = x;
              break;
          }
          console.log(':' + info);
          Message({
            message: ':' + info,
            type: 'error',
            duration: 5 * 1000
          })
          reject(info)
        }
      }, at
    )
  })
}



